export default {
	name: "Nav",
	data() {
		return {
			id: '',
			class_id: '',
			whichDetail: 'picture'
		}
	},
	props: {
		navList: {
			default () {
				return [{
					title: '图解',
					path: 'picture'
				}, {
					title: '动画',
					path: 'video'
				}]
			},
			type: Array
		}
	},
	onLoad(option) {

	},
	methods: {
		changeType(witch) {
			this.whichDetail = witch
			this.$emit('changeType', witch)
		}
	},
	computed: {
		isCurrentRoute() {
			return function(path) {
				return path === this.whichDetail
			}
		}
	}
}
